import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import Select from "react-select";
import { gsap } from "gsap";
import { ScrollSmoother } from "gsap/ScrollSmoother";
import { device } from "../devices";

export const BodyBg = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-color: ${(props) => props.theme.yellow};
  overflow: hidden;
`;
export const Wrap = styled.div`
  width: 95%;
  height: 85%;
  margin: auto;
  background-color: white;
  border: 5px dotted black;
  text-align: center;
  position: relative;
  display: flex;
`;
export const StoreName = styled.h2`
  font-family: "Bangers", cursive;
  position: absolute;
  color: red;
  left: 50%;
  transform: translateX(-50%);
  top: -8%;
  font-size: 6vmax;
  -webkit-text-stroke: black 0.02em;
  text-shadow: -0.04em 0.03em 0 black, -0.08em 0.05em 0 black;
`;
const StoreForm = styled.form`
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
export const Selection = styled(Select)`
  font-size: 1.5vmax;
  margin-bottom: 10%;
  text-transform: uppercase;
`;
export const FoodPic = styled.div`
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(9, 100%);
  margin: auto;
  height: 70%;
  width: 50%;
  div {
    opacity: 0;
    height: 100%;
    width: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
  }
`;
export const Button = styled.button`
  cursor: pointer;
  text-transform: uppercase;
  font-size: 2vmax;
  color: black;
  padding: 1% 2.5%;
  border-radius: 5rem;
  border: none;
  background-color: #fe5207;
  /* background: repeating-linear-gradient(
    45deg,
    ${(props) => props.theme.yellow},
    ${(props) => props.theme.yellow} 30px,
    #ffbb37 0,
    #ffbb37 60px
  ); */
`;
function Store() {
  gsap.registerPlugin(ScrollSmoother);
  const [store, setStore] = useState("");
  const focus = useRef();
  const navigate = useNavigate();
  const imageLink = [
    "taco.png",
    "breakfast-burrito.png",
    "burritos.png",
    "churros.png",
    "enchiladas.png",
    "mexican-rice.png",
    "nachos.png",
    "salad-bowl.png",
    "tamales.png",
  ];

  useEffect(() => {
    const mastertl = gsap.timeline({
      repeat: -1,
    });
    let targets = gsap.utils.toArray(".mainPic");

    targets.forEach((target, i) => {
      let tl = gsap.timeline({});
      tl.to(target, {
        xPercent: -100 * i,
        duration: 1.5,
        ease: "Power0.easeIn",
        opacity: 1,
      }).to(target, {
        delay: 3,
        xPercent: -100 * (i + 1),
        duration: 2,
        ease: "Power0.easeIn",
      });
      mastertl.add(tl);
    });
  });
  const goToStore = (event) => {
    event.preventDefault();
    store === "" && focus.current.focus();
    navigate(`${store}?menu=main`);
  };
  const stores = [
    { value: "korea", label: "Korea" },
    { value: "mexico", label: "Mexico" },
    { value: "usa", label: "Usa" },
  ];
  const handleChange = (event) => {
    setStore(event.value);
  };

  return (
    <BodyBg>
      <Wrap>
        <StoreName>almighty Taco</StoreName>
        <FoodPic>
          {imageLink.map((link, i) => (
            <div
              key={i}
              className="mainPic"
              style={{
                backgroundImage: `url(https://youbine.github.io/almightyTaco/images/${link})`,
              }}
            ></div>
          ))}
        </FoodPic>
        <StoreForm onSubmit={goToStore}>
          <Selection
            placeholder="Please pick store to Order"
            onChange={handleChange}
            options={stores}
            ref={focus}
          />
          <Button type="submit">Go grab taco</Button>
        </StoreForm>
      </Wrap>
    </BodyBg>
  );
}
export default Store;
